import "antd/dist/antd.css";

import {
  Card,
  Input,
  Button,
  Space,
  Row,
  Col,
  Popconfirm,
  Tooltip,
} from "antd";
import { useCallback, useEffect, useState } from "react";
import useLocalStorage from "use-local-storage";
import { ClearOutlined, DeleteOutlined } from "@ant-design/icons";
import { SizeType } from "antd/lib/config-provider/SizeContext";

type Task = {
  title: string;
  seconds: number;
};

function toTime(seconds: number) {
  const date = new Date(0);
  date.setSeconds(seconds);
  return date.toISOString().substr(11, 8);
}

const DEFAULT_TASKS = [
  "Планирование",
  "Разработка",
  "ИФТ+ПСИ",
  "Внедрение",
  "DevOps",
  "Коммуникация",
  "Работа на метрики",
  "Поддержка (ТП)",
];

export default function App() {
  const [tasks, setTasks] = useLocalStorage<Task[]>(
    "tasks",
    DEFAULT_TASKS.map((title) => ({ title, seconds: 0 }))
  );

  const [newTitle, setNewTitle] = useState("");

  const [activeTask, setActiveTask] = useLocalStorage<string>(
    "active-task",
    ""
  );

  const totalSeconds = tasks.reduce((acc, cur) => (acc += cur.seconds), 0);

  useEffect(() => {
    document.title = `Налог-трекер: ${toTime(totalSeconds)}`;
  }, [totalSeconds]);

  const updateTask = useCallback(
    (title: string, seconds: number) => {
      setTasks((tasks = []) => [
        ...tasks.map((t) =>
          t.title !== title ? { ...t } : { ...t, seconds: t.seconds + seconds }
        ),
      ]);
    },
    [setTasks]
  );

  useEffect(() => {
    let past = new Date().getTime();

    const interval = setInterval(() => {
      const now = new Date().getTime();

      const seconds = Math.round((now - past) / 1000);

      updateTask(activeTask, seconds);

      past = now;
    }, 1000);

    return () => {
      clearInterval(interval);
    };
  }, [activeTask, updateTask]);

  const addTask = () => {
    if (newTitle.trim() === "") {
      return;
    }

    if (tasks.some((t) => t.title === newTitle)) {
      return;
    }

    setTasks((tasks = []) => [...tasks, { title: newTitle, seconds: 0 }]);
    setNewTitle("");
  };

  const deleteTask = (title: string) => {
    setTasks((tasks = []) => [...tasks.filter((t) => t.title !== title)]);
  };

  const clearSeconds = () => {
    setTasks((tasks = []) => [...tasks.map((t) => ({ ...t, seconds: 0 }))]);
  };

  const renderClearSeconds = (size?: SizeType) => (
    <Tooltip title="Сбросить время">
      <Popconfirm
        title="Точно?"
        cancelText="Нет"
        okText="Да"
        onConfirm={(event) => {
          clearSeconds();
          setActiveTask("");
          event?.stopPropagation();
        }}
      >
        <Button icon={<ClearOutlined />} size={size} />
      </Popconfirm>
    </Tooltip>
  );

  return (
    <Row style={{ padding: "2rem" }}>
      <Col xs={0} sm={0} md={2}>
        <Space direction="vertical" size="middle">
          <Row>
            <Col>{renderClearSeconds()}</Col>
          </Row>
        </Space>
      </Col>

      <Col xs={24} sm={24} md={12} lg={10} xl={8}>
        <Space direction="vertical" size="middle" style={{ display: "flex" }}>
          {tasks
            .sort((a, b) => b.seconds - a.seconds)
            .map((task) => {
              const isActive = task.title === activeTask;

              const onClick = () => {
                if (isActive) {
                  setActiveTask("");
                } else {
                  setActiveTask(task.title);
                }
              };

              return (
                <Card
                  size="small"
                  style={{ cursor: "pointer" }}
                  key={task.title}
                  title={task.title}
                  onClick={onClick}
                  headStyle={{
                    backgroundColor: isActive ? "#e5ffa3" : "initial",
                  }}
                  bodyStyle={{
                    backgroundColor: isActive ? "#f7ffe4" : "initial",
                  }}
                  extra={
                    <span onClick={(event) => event.stopPropagation()}>
                      <Popconfirm
                        title="Точно?"
                        onConfirm={(event) => {
                          deleteTask(task.title);
                          event?.stopPropagation();
                        }}
                        onCancel={(event) => event?.stopPropagation()}
                        okText="Да"
                        cancelText="Нет"
                      >
                        <Button shape="circle" icon={<DeleteOutlined />} />
                      </Popconfirm>
                    </span>
                  }
                >
                  {toTime(task.seconds)}
                </Card>
              );
            })}
        </Space>
        <Row style={{ marginTop: "1rem" }}>
          <Col xs={3} sm={3} md={0}>
            {renderClearSeconds("large")}
          </Col>
          <Col xs={15} sm={15} md={18}>
            <Input
              maxLength={256}
              size="large"
              value={newTitle}
              onChange={(event) => setNewTitle(event.target.value)}
              placeholder="Новая активность"
              onPressEnter={addTask}
            />
          </Col>
          <Col span={6}>
            <Button
              size="large"
              type="primary"
              onClick={addTask}
              style={{ width: "100%" }}
            >
              Добавить
            </Button>
          </Col>
        </Row>
      </Col>
    </Row>
  );
}
